import React from "react";

const SECTIONS = [
    {
        icon: './icon/triangle.svg',
        title: 'Toán',
        quantity: 205,
    },
    {
        icon: './icon/blackboard.svg',
        title: 'Vật lý',
        quantity: 20,
    },
    {
        icon: './icon/atom.svg',
        title: 'Hóa học',
        quantity: 2010,
    },
    {
        icon: './icon/gdp.svg',
        title: 'Kinh tế',
        quantity: 2,
    },
    {
        icon: './icon/double-decker.svg',
        title: 'Tiếng anh',
        quantity: 35,
    }
];

class TopSection extends React.Component {
    render() {
        let content = SECTIONS.map(generateTopActiveUser);
        return (
            <div className={'tm-sidebar-frame'}>
                <div className={'tm-sidebar-right-title'}>Top Sections</div>
                <div className={'tm-sidebar-right-deco'}/>
                {content}
            </div>
        );
    }
}

function generateTopActiveUser(sectionInfo) {
    return (
        <div className={'tm-float-clear tm-margin-10'}>
            <img className={'tm-topuser-img'} src={sectionInfo.icon} alt={'Avatar'}/>
            <div className={'tm-topuser-name'}>{sectionInfo.title}</div>
        </div>
    );
}

export default TopSection;
