import React from 'react';
import {BrowserRouter as Router, Route, Switch, Link, Redirect} from "react-router-dom";
import Auth from "./components/Authenticate/auth";
import MainPage from "./components/Dashboard/MainPage";
import {connect} from "react-redux";

class WebApp extends React.Component {
    render() {
        return (
            <Router>
                <Switch>
                    <Route path={'/login'}>
                        <Auth type={'login'}/>
                    </Route>
                    <Route path={'/register'}>
                        <Auth type={'register'}/>
                    </Route>
                    <Route path={'/dashboard'}>
                        <MainPage type={'dashboard'}/>
                    </Route>
                    <Route path={'/question'}>
                        <MainPage type={'question'}/>
                    </Route>
                    <Route path={'/question-detail'}>
                        <MainPage type={'detail'}/>
                    </Route>
                    <Route path={'/create-group'}>
                        <MainPage type={'create-group'}/>
                    </Route>
                    <Route path={'/group-dashboard'}>
                        <MainPage type={'group-dashboard'} />
                    </Route>
                    <Route path={'/'}>
                        <Redirect to={'/login'}/>
                    </Route>
                </Switch>
            </Router>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        authenticated: state.authenticated
    }
};

const mapDispatchToProps = (dispatch) => {
    return {};
};

const App = connect(mapStateToProps, mapDispatchToProps)(WebApp);

export default App;
