import React from "react";

class Topics extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            All: true,
            Math: false,
            Literature: false,
            Physic: false,
            PHP: false
        };
        // this.handleClick = this.handleClick.bind(this);
    }

    handleClick(name) {
        switch (name) {
            case 'All':
                return this.setState({
                    All: true,
                    Math: false,
                    Literature: false,
                    Physic: false,
                    PHP: false
                });
            case 'Math':
                return this.setState({
                    All: false,
                    Math: true,
                    Literature: false,
                    Physic: false,
                    PHP: false
                });
            case 'Literature':
                return this.setState({
                    All: false,
                    Math: false,
                    Literature: true,
                    Physic: false,
                    PHP: false
                });
            case 'Physic':
                return this.setState({
                    All: false,
                    Math: false,
                    Literature: false,
                    Physic: true,
                    PHP: false
                });
            case 'PHP':
                return this.setState({
                    All: false,
                    Math: false,
                    Literature: false,
                    Physic: false,
                    PHP: true
                });
            default:
                return this.setState({
                    All: true,
                    Math: false,
                    Literature: false,
                    Physic: false,
                    PHP: false
                });
        }
    }

    render() {
        let active = 'tm-db-ct-yq-topic tm-topic-active';
        let inactive = 'tm-db-ct-yq-topic tm-topic-inactive';
        let decoActive = 'tm-deco-topics-active';
        let decoInactive = 'tm-deco-topics-inactive';

        return (
            <div className={'tm-db-ct-yq-topics-frame tm-flex-row-spacebtw-nowrap'}>
                <div onClick={this.handleClick.bind(this, 'All')}
                     className={this.checkActive('All') ? active : inactive}>
                    <i className="fas fa-globe-americas tm-db-ct-yq-topic-icon"/>All
                    <div className={this.checkActive('All') ? decoActive : decoInactive}/>
                </div>
                <div onClick={this.handleClick.bind(this, 'Math')}
                     className={this.checkActive('Math') ? active : inactive}>
                    <i className="fas fa-calculator tm-db-ct-yq-topic-icon"/>Math
                    <div className={this.checkActive('Math') ? decoActive : decoInactive}/>
                </div>
                <div onClick={this.handleClick.bind(this, 'Literature')}
                     className={this.checkActive('Literature') ? active : inactive}>
                    <i className="fas fa-book tm-db-ct-yq-topic-icon"/>Literature
                    <div className={this.checkActive('Literature') ? decoActive : decoInactive}/>
                </div>
                <div onClick={this.handleClick.bind(this, 'Physic')}
                     className={this.checkActive('Physic') ? active : inactive}>
                    <i className="fas fa-atom tm-db-ct-yq-topic-icon"/>Physic
                    <div className={this.checkActive('Physic') ? decoActive : decoInactive}/>
                </div>
                <div onClick={this.handleClick.bind(this, 'PHP')}
                     className={this.checkActive('PHP') ? active : inactive}>
                    <i className="fab fa-php tm-db-ct-yq-topic-icon"/>PHP
                    <div className={this.checkActive('PHP') ? decoActive : decoInactive}/>
                </div>
            </div>
        );
    }

    checkActive(name) {
        let names = this.state;

        switch (name) {
            case 'All':
                return names.All;
            case 'Math':
                return names.Math;
            case 'Literature':
                return names.Literature;
            case 'Physic':
                return names.Physic;
            case 'PHP':
                return names.PHP;
            default:
                return names.All;
        }
    }
};

export default Topics;