import React from "react";

const USER = [
    {
        name: "Nguyen Tien Manh",
        avatar: "./icon/double-decker.svg"
    },
    {
        name: "Nguyen Thi mung",
        avatar: "./icon/double-decker.svg"
    },
    {
        name: "Bui Van Hung",
        avatar: "./icon/double-decker.svg"
    },
    {
        name: "Ta Thi Bich Ngoc",
        avatar: "./icon/double-decker.svg"
    },
    {
        name: "Phan Thi Hong Nhung",
        avatar: "./icon/double-decker.svg"
    },
];

class TopActiveUser extends React.Component {
    render() {
        let content = USER.map(generateTopActiveUser);
        return (
            <div className={'tm-sidebar-frame'}>
                <div className={'tm-sidebar-right-title'}>Top users</div>
                <div className={'tm-sidebar-right-deco'}/>
                {content}
            </div>
        );
    }
}

function generateTopActiveUser(userInfo) {
    return (
        <div className={'tm-float-clear tm-margin-10'}>
            <img className={'tm-topuser-img'} src={userInfo.avatar} alt={'Avatar'}/>
            <div className={'tm-topuser-name'}>{userInfo.name}</div>
        </div>
    );
}

export default TopActiveUser;
