import React from "react";
import CKEditor from '@ckeditor/ckeditor5-react';
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import GenerateTag from "../Common/GenerateTag";

const CATEGORIES = [
    'Math',
    'Psychology',
];

class GroupCreateQuestion extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            listTags: [],
            input: ''
        };
        this.handleInputTag = this.handleInputTag.bind(this);
    }

    handleInputTag = () => {
        this.setState({
            listTags: this.state.listTags.concat([this.state.input]),
            input: ''
        })
    };

    handleEnter = (event) => {
        if (event.key === 'Enter') {
            this.handleInputTag(event);
        }
    };

    handleChange = (event) => {
        this.setState({
            input: event.target.value,
        })
    };

    render() {
        let listCategories = CATEGORIES.map(function (item) {
            return <option value={item}>{item}</option>
        });

        let tags = this.state.listTags.map(function (item) {
            return <GenerateTag name={item}/>
        });

        return (
            <div>
                <input className={'tm-newqt-title'} type={'input'} placeholder={'Tiêu đề bài viết'}/>
                <CKEditor
                    editor={ClassicEditor}
                    onInit={editor => {
                        // You can store the "editor" and use when it is needed.
                        console.log('Editor is ready to use!', editor);
                    }}
                    onChange={(event, editor) => {
                        const data = editor.getData();
                        console.log({event, editor, data});
                    }}
                    onBlur={(event, editor) => {
                        console.log('Blur.', editor);
                    }}
                    onFocus={(event, editor) => {
                        console.log('Focus.', editor);
                    }}
                />
                <div className={'tm-newqt-category'}>
                    <div className={'tm-newqt-ctg-title'}>Categories</div>
                    <select className={'tm-newqt-select-box'}>
                        {listCategories}
                    </select>
                </div>
                <div className={'tm-newqt-setcoin'}>
                    <div className={'tm-newqt-coin-title'}>Số coin cho câu hỏi</div>
                    <input type={'number'}/>
                </div>
                <div className={'tm-newqt-tags'}>
                    <div className={'tm-newqt-ctg-title'}>Tags</div>
                    <div className={'tm-newqt-tags-list'}>
                        {tags}
                        <input
                            value={this.state.input}
                            type={'input'}
                            className={'tm-tag-frame tm-tag-input'}
                            placeholder={'Enter your tag'}
                            onKeyDown={this.handleEnter.bind(this)}
                            onChange={this.handleChange.bind(this)}
                        />
                    </div>
                </div>
                <div className={'tm-newqt-btn-frame'}>
                    <button className={'tm-btn tm-btn-success tm-float-right '}>Create</button>
                    <button onClick={this.props.toggleForm} className={'tm-btn tm-btn-cancel tm-float-right '}>Cancel</button>
                </div>
            </div>
        );
    }
}

export default GroupCreateQuestion;