import React from "react";
import {connect} from "react-redux";
import {LOGOUT} from "../../../Reducers/actions";
import {Redirect} from "react-router";

class InfosComponent extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            redirectTo: '',
        }
    }

    handleSignOut() {
        this.props.signOut(LOGOUT);
        this.setState({
            redirectTo: '/login'
        })
    }

    render() {
        if (this.state.redirectTo !== '') {
            return <Redirect to={this.state.redirectTo}/>
        }

        return (
            <div className={'tm-db-hd-info-frame'}>
                <a href={'/dashboard'} className={'tm-db-hd-nameweb'}>ShareKnowledge</a>
                <div className={'tm-db-hd-right'}>
                    <div className={'tm-db-hd-notification tm-flex-column'}>
                        <i className="far fa-bell"/>
                    </div>
                    <div className={'tm-db-hd-nameinfo tm-flex-column'}>
                        <div className={'tm-db-hd-name'}>Nguyễn Tiến Mạnh</div>
                        <div className={'tm-db-hd-status'}>Online<i className={'tm-db-hd-status-icon'}/></div>
                    </div>
                    <div className={'tm-db-hd-avatar tm-flex-column'}>
                        <div className={'tm-db-hd-image'}/>
                    </div>
                    <div className={'tm-db-hd-signout tm-flex-column'} onClick={this.handleSignOut.bind(this)}>
                        <i className="fas fa-sign-out-alt"></i>
                    </div>
                </div>
            </div>
        );
    }
}

const signOutAction = (action) => {
    return {
        type: action,
    }
};

const mapStateToProps = (state) => {
    return {}
};

const mapDispatchToProps = (dispatch) => {
    return {
        signOut: (action) => {
            dispatch(signOutAction(action))
        }
    }
};

const Infos = connect(mapStateToProps, mapDispatchToProps)(InfosComponent);

export default Infos;