import React from "react";
import YourQuestion from "./Content/YourQuestion";
import CreateQuestion from "./Content/CreateQuestion";
import Topics from "./Content/Topics";
import NewestQuestion from "./Content/NewestQuestion";
import SidebarLeft from "./Sidebar/SidebarLeft";
import SidebarRight from "./Sidebar/SidebarRight";
import NewQuestion from "./NewQuestion";
import QuestionDetail from "./QuestionDetail";
import CreateGroup from "./Group/CreateGroup";
import GroupLeft from "./Sidebar/GroupLeft";
import GroupRight from "./Sidebar/GroupRight";
import GroupDashboard from "./Group/GroupDashboard";

const TYPE = {
    DASHBOARD: 'dashboard',
    QUESTION: 'question',
    DETAIL: 'detail',
    CREATE_GROUP: 'create-group',
    GROUP_DASHBOARD: 'group-dashboard'
};

class Content extends React.Component {
    render() {
        switch (this.props.type) {
            case TYPE.DASHBOARD:
                return (
                    <div className={'tm-db-content-frame'}>
                        <SidebarLeft />
                        <SidebarRight />
                        <YourQuestion />
                        <NewestQuestion />
                        <Topics />
                        <CreateQuestion />
                    </div>
                );
            case TYPE.QUESTION:
                return (
                    <div className={'tm-db-content-frame'}>
                        <SidebarLeft />
                        <NewQuestion />
                        <SidebarRight />
                    </div>
                );
            case TYPE.DETAIL:
                return (
                    <div className={'tm-db-content-frame'}>
                        <SidebarLeft />
                        <QuestionDetail />
                        <SidebarRight />
                    </div>
                );
            case TYPE.CREATE_GROUP:
                return (
                    <div className={'tm-db-content-frame'}>
                        <SidebarLeft />
                        <CreateGroup />
                        <SidebarRight />
                    </div>
                );
            case TYPE.GROUP_DASHBOARD:
                return (
                    <div className={'tm-db-content-frame'}>
                        <GroupLeft />
                        <GroupDashboard />
                        <GroupRight />
                    </div>
                );
        }
    }
}

export default Content;