import React from 'react';
import Question from "../Question";
import Topics from "../Topics";

class YourQuestion extends React.Component {
    render() {
        let questionInfo = {
            title: 'This is question',
            content: 'Khó mà tìm được một người tốt gồm 10 truyện ngắn tập trung khắc họa đời sống thường nhật của người dân ở miền Nam nước Mỹ. Một gia đình nhỏ trong một chuyến đi đến Florida; hai ông cháu từ miền quê lần đầu đi tàu ra thành phố, lần đầu nhìn thấy những người da màu; một anh chàng bán Kinh thánh rong nay đây mai đó; một gia đình người tị nạn đến giúp việc ở một trang trại nhỏ… Những câu chuyện rất đỗi bình thường ấy lại ẩn chứa những kịch tính bất ngờ, nơi yếu tố bạo lực làm rúng động đức tin, nơi sự tàn nhẫn của con người sinh ra từ mông muội, nơi cái ác được thực thi một cách thản nhiên do những ghen tị nhỏ nhen mù quáng…',
            created_at: '2020-02-03',
            view: 231,
            replied: 12,
            upvote: 0,
            status: 1
        };

        let content = [];

        for (let i = 0; i < 4; ++i) {
            content.push(<Question keyIndex={i} questionInfo={questionInfo}/>);
        }

        return (
            <div className={'tm-db-ct-yq-frame'}>
                <div className={'tm-db-ct-yq-title'}>Câu hỏi của bạn</div>
                <Topics/>
                <div className={'tm-flex-row-spacebtw-wrap'}>
                    {content}
                </div>
            </div>
        );
    }
}

export default YourQuestion;